@import "variables.scss";

body {
  // background-color: $almost-black;
  // background-image: url(../../PixelArt.png);
}

#main-body {
  display: grid;
  grid-template-columns: 3fr 1fr;
  & h1 {
    grid-column-end: span 2;
  }
}

#results-body {
  display: flex;
  justify-content: space-around;
}

#display-container {
  width: 80%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  & h2 {
    grid-column-start:1;
    grid-column-end: span 2;
  }
}

#user-deck-container {
  text-align: center;
  height: fit-content;
  position: sticky;
  top: 10px;
  transition: 0.3s ease-in-out;
  background-color: #cce;
  max-height: 90vh;
  overflow: scroll;
}

.user-deck-contracted {
  height: 100px;
}

.user-deck-expanded {
  height: 600px;
}